@import-normailize ;
@import "helper";

* {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

*::before {
  box-sizing: border-box;
}

*::after {
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
  background-color: #f5f5f5;
}
button,input{
  font: inherit;
  background-color: inherit;
}
.icon{
  width: 1em;
  height: 1em;
  display: inline-block;
}
:focus{
  outline: none;
}
.clearfix::after{
  content: '';
  display: block;
  clear: both;
}
.oneLine{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}